<template>
    <div v-if="!image" class="dragContainer">
        <div class="dragImg-area" @dragenter="hovering = true" @dragleave="hovering = false" :class="{'dropzone-hover': hovering}">
            <div class="dragImg-text">
                <span class="dragImg-title">Drop image here or click to select</span>
                <span class="dragImg-info" v-if="info">16:9</span>
            </div>
            <input type="file" @change="onImgUpload">
        </div>
    </div>
    <div v-else class="dragImg-preview">
        <img :src="image" />
        <div class="dragImg-close modal-close" @click="removeImage">
            <span></span>
            <span></span>
        </div>
    </div>
</template>

<script>
export default {

  data() {
    return {
      image: '',
      hovering: false,
    };
  },

  methods: {
    onImgUpload(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
      this.image = files[0];
    },
    createImage(file) {
      // const image = new Image();
      const reader = new FileReader();
      const vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
        vm.hovering = false;
      };
      reader.readAsDataURL(file);
    },
    removeImage() {
      this.image = '';
    },
  },
};
</script>

<style scoped lang='scss'>
@import "../scss/app.scss";

.dragContainer {
  width: 100%;
  margin: 1.5rem 0;
}

</style>



