<template>
    <input type="file" name="file" id="simple" class="upload" @change="onFileUpload">
    <label for="simple">
      <span v-if="!count">choose a file...</span>
      <span v-else>file seleced!</span>
    </label>
</template>

<script>

export default {

  data() {
    return {
      count: 0,
      className: [
        {
          type: 'upload-default',
          content: 'choose a file...',
        },
        {
          type: 'upload-active',
          content: 'choose more!',
        },
        {
          type: 'upload-drag',
          content: 'drag in!',
        },
      ],
    };
  },
  methods: {
    onFileUpload(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (files.length) {
        this.count = files.length;
      }
      return;
    },
  },
};
</script>

<style scoped lang='scss'>
@import "../scss/app.scss";

.demo-text {
  margin: 1.5rem 0;
}

</style>



