<template>
  <div class="Button demo-section">
    <div class="demo-header">
      <h1>Buttons</h1>
    </div>
    <div class="demo-text">
      <button v-for="d in className" class="button {{ d.type }}">
        {{ d.content }}
      </button>
    </div>

  </div>
</template>

<script>
export default {

  data() {
    return {
      className: [
        {
          type: 'button-default',
          content: 'simple',
        },
        {
          type: 'button-active',
          content: 'Great!',
        },
        {
          type: 'button-warning',
          content: 'woops?',
        },
        {
          type: 'button-danger',
          content: 'Fuck!',
        },
        {
          type: 'button-disable',
          content: 'NO WAY',
        },
      ],
    };
  },
};
</script>

<style scoped lang='scss'>
@import "../scss/app.scss";

</style>



